<template>
    <div id="app">
      <!-- 메인 제목: 과일 선택 -->
      <h1>과일 선택</h1>
  
      <!-- 체크박스 리스트 영역 -->
      <!-- fruits 배열의 각 항목을 반복하여 체크박스를 생성 -->
      <div class="checkbox-container" v-for="fruit in fruits" :key="fruit.id">
        <!-- 체크박스와 레이블을 포함한 구조 -->
        <label class="checkbox-label">
          <!-- 체크박스 input -->
          <!-- :value로 fruit.name 설정, v-model로 selectedFruits에 바인딩 -->
          <input 
            type="checkbox" 
            :value="fruit.name" 
            v-model="selectedFruits" 
            class="custom-checkbox"
          />
          <!-- 체크박스의 커스텀 디자인을 위한 영역 -->
          <span class="checkbox-custom"></span>
          <!-- 체크박스 옆에 표시되는 과일 이름 -->
          {{ fruit.name }}
        </label>
      </div>
  
      <!-- 선택된 과일 출력 영역 -->
      <h2>선택된 과일:</h2>
      <div class="selected-container">
        <!-- 선택된 과일이 없을 때 표시되는 메시지 -->
        <p v-if="selectedFruits.length === 0">아직 선택된 과일이 없습니다.</p>
        <!-- 선택된 과일이 있을 때 출력하는 리스트 -->
        <ul v-else>
          <li v-for="fruit in selectedFruits" :key="fruit">
            <!-- 선택된 과일 이름 출력 -->
            {{ fruit }}
          </li>
        </ul>
      </div>
    </div>
  </template>
  
  <script>
    export default {
      name: "App", // 컴포넌트 이름
      data() {
        return {
          // 체크박스에 표시할 과일 목록 배열
          // 각 항목은 id와 name 속성을 포함
          fruits: [
            { id: 1, name: "사과" },
            { id: 2, name: "망고" },
            { id: 3, name: "바나나" },
            { id: 4, name: "포도" },
            { id: 5, name: "딸기" }
          ],
          // 선택된 과일을 저장할 배열
          // 사용자가 체크박스를 클릭할 때 업데이트됨
          selectedFruits: []
        };
      }
    };
    </script>
  
